/*
 * ARK Bootstrap Theme
 *
 * This file can be used to theme Bootstrap after it has been built.
 *
 * Bootstrap examples/dashboard/dashboard.css
 */

/*
 * Resposive settings
 */

$break-small: 767px;
$break-large-tablet: 926px;
$break-tablet: 1199px;
@media (min-width: $break-tablet) {
    .collapse {
        display: block;
    }
}

/*
 * Base structure
 */
html {}

body {
    margin-bottom: 60px;
}

/*
 * Global add-ons
 */

.sub-header {
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
}

/*
* Icons
*/

.dime-icon {
    position: relative;
    top: 1px;
    display: inline-block;
    font-style: normal;
    font-weight: normal;
    line-height: 1;
    float: left;
    background-size: cover;
    display: block;
    /* This is required */
}

.dime-icon.normal {
    width: 32px;
    height: 32px;
}

.dime-icon.small {
    width: 16px;
    height: 16px;
}

.dime-icon.medium {
    width: 64px;
    height: 64px;
}

.dime-icon.large {
    width: 128px;
    height: 128px;
}

.dime-icon.icon-user {
    background-image: url("../images/icons/logged_in_.png");
    margin-right: 1em;
}

.dime-icon.icon-user-edit {
    background-image: url("../images/icons/edit_user.png");
    margin: 0 25px;
    float: none;
}

.dime-icon.icon-add {
    background-image: url("../images/icons/add_find.png");
}

.dime-icon.icon-search {
    background-image: url("../images/icons/search_find.png");
}

.dime-icon.icon-map {
    background-image: url("../images/icons/map.png");
}

.dime-icon.icon-heatmap {
    background-image: url("../images/icons/heatmap.png");
}

/*
 * Top navigation
 * Hide default border to remove 1px line.
 */

.navbar-fixed-top {
    border: 0;
}

.navbar {
    background: url("../images/brand/header.png") no-repeat;
    background-size: cover;
    border-radius: 0;
    border: none;
    height: 120px;
    margin-bottom: 0;
}

.dime-nav {
    position: absolute;
    top: 35px;
}
@media (max-width: $break-large-tablet) {
    .navbar {
        height: 138px;
    }
}
@media (max-width: $break-small) {
    .navbar {
        height: 250px;
    }

    .dime-nav {
        top: 0;
    }
}

.dime-nav li {
    vertical-align: bottom;
    text-align: center;
    border: none;
}

.dime-nav li a {
    display: inline;
}

.dime-nav.nav-tabs li a:focus,
.dime-nav.nav-tabs li.active a:focus .dime-nav.nav-tabs li a:hover,
.dime-nav.nav-tabs li.active a:hover,
.dime-nav.nav-tabs-justified li a,
.dime-nav.nav-tabs.nav-justified li a .dime-nav.nav-tabs li.active a {
    background: none;
    box-shadow: none;
    border: none;
    padding: 0;
}

.dime-nav.nav-tabs li.active a:focus,
.dime-nav.nav-tabs li.active a:hover {
    cursor: pointer;
}

.dime-nav.nav-tabs li:hover .dropdown-menu {
    display: block;
}

.dime-nav.nav-tabs li:hover .dropdown-menu a {
    line-height: 2.5em;
}

.dime-nav.nav-tabs .dropdown-menu {
    margin-top: -1px;
    border-radius: 5px;
    background-color: #ececec;
    border: 1px solid black;
    min-width: 100%;
}

.dime-nav.nav-tabs li.active a {
    color: #fff;
}

.dime-nav a {
    color: #fff;
    text-transform: uppercase;
}

.dime-banner {
    text-align: center;
    height: 80px;
}
@media (max-width: $break-tablet) {
    .dime-banner .navbar-toggle {
        display: block;
        float: none;
        z-index: 1;
    }
}

.dime-top {
    display: inline-flex;
}

.dime-title {
    background: url("../images/brand/logo.png");
    display: inline-flex;
    background-size: contain;
    line-height: 64px;
    width: 270px;
}

/*
 * Sidebar make fixed width when screen is too small
 */
@media (max-width: $break-tablet) {
    .sidebar {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        z-index: 1000;
        display: none;
        padding: 20px;
        overflow-x: hidden;
        overflow-y: auto;
        /* Scrollable contents if viewport is shorter than content. */
        background-color: #fff;
        width: 150px;
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.176);
    }
}

/*
 * Sidebar navigation
 */

.nav-sidebar {
    margin-right: -21px;
    /* 20px padding + 1px border */
    margin-bottom: 20px;
    margin-left: -20px;
}

.nav-sidebar > li > a {
    padding-right: 20px;
    padding-left: 20px;
}

.nav-sidebar > .active > a,
.nav-sidebar > .active > a:focus,
.nav-sidebar > .active > a:hover {
    color: #fff;
    background-color: #428bca;
}

/*
 * User tool
 */

.usertool {
    padding: 20px;
    min-height: 120px;
    text-transform: uppercase;
    text-align: center;
}

.dime-listhead {
    display: inline-flex;
    position: relative;
}

.dime-listhead.normal {
    line-height: 32px;
}

.dime-listhead.small {
    line-height: 16px;
}

.dime-navlist {
    text-align: center;
    box-shadow: none;
}

.dime-navlist li {
    border: medium none;
}

.dime-navlist li a {
    color: black;
}

.dime-navlist li.active,
.dime-navlist li.active:focus,
.dime-navlist li.active:hover {
    background: none;
    border: none;
    font-weight: 600;
    text-shadow: none;
}

/*
* Login
*/

.loginmodal-container.dime {
    border: 1px solid black;
    background-image: linear-gradient(0deg, #f4f4f4, #c3c3c3);
    text-align: center;
    padding: 50px 10px;
}

.loginmodal-container.dime input {
    border: 1px solid black;
    display: block;
    margin: 15px auto;
    width: 250px;
}

.modal-dialog {
    margin: 300px auto;
    width: 400px;
}

.usertool a {
    color: #c3c3c3;
    weight: 100;

}

a.loginbutton {
    background-image: url("../images/icons/login_button.png");
    background-size: contain;
    display: block;
    height: 2em;
    width: 5.75em;
}

/*
 * Main content
 */

.main {
    padding: 20px;
    background-color: #f9f9f9;
}
@media (min-width: 768px) {
    .main {
        padding-right: 40px;
        padding-left: 40px;
    }
}

.content-col-1 {
    border-right: 1px #cecece solid;
}

.content-col-1,
.content-col-2 {
    padding: 20px;
}

.form-horizontal .form-group label {
    height: 1.4em;
    padding: 0;
}

.form-group input {
    border: 1px solid black;
    border-radius: 20px;
    height: 1.4em;
    line-height: 1.4em;
    vertical-align: middle;
    padding: 0 10px;
}

#dime_find_item_dime_find_finddate_finddate {
    display: inline-flex;
}

.select2-container {
    margin: 5px;
}

.dime-table tbody,
.dime-table thead {
    text-transform: uppercase;
    border: 1px black solid;
}

.dime-table tbody tr,
.dime-table thead tr {
    line-height: 1.2em;
}

.dime-table tbody tr.selected td {
    background-color: #999999;
}

.dime-table .card-views {
    text-align: center;
}

.dime-table .card-views .title {
    display: none;
}

.dime-table.cardViewTable,
.dime-table.cardViewTable tbody {
    border: none;
}

.dime-table.cardViewTable tr {
    display: block;
    float: left;
}

.dime-table.cardViewTable td {
    display: block;
}

.bootstrap-table .table:not(.table-condensed).dime-table tbody td,
.bootstrap-table .table:not(.table-condensed).dime-table thead th {
    border-left: none;
    border-bottom: 1px black solid;
    padding-top: 0;
    padding-bottom: 0;
}

.bootstrap-table .table:not(.table-condensed).dime-table.cardViewTable tbody td,
.bootstrap-table .table:not(.table-condensed).dime-table.cardViewTable thead th {
    border-bottom: none;
    border-top: none;
    padding: 5px;
}

.fixed-table-container {
    border: none;
}

.fixed-table-container .dime-table thead th .both {
    background-image: none;
}

.fixed-table-container .dime-table thead th .asc {
    background-repeat: no-repeat;
    background-image: url("data:image/png;base64,");
}

.fixed-table-container .dime-table thead th .desc {
    background-repeat: no-repeat;
    background-image: url("data:image/png;base64,");
}

td.thumbimage,
th.thumbimagehead {
    display: none;
}

.card-views .thumbimage {
    display: block;
}

.search_map {
    height: 600px;
}

/*
 * Placeholder dashboard ideas
 */

.placeholders {
    margin-bottom: 30px;
    text-align: center;
}

.placeholders h4 {
    margin-bottom: 0;
}

.placeholder {
    margin-bottom: 20px;
}

.placeholder img {
    display: inline-block;
    border-radius: 50%;
}

/*
Dime Home Stuff
*/

.dimehome {
    text-align: center;
}

.dimehome.banner {
    text-align: center;
    font-weight: 800;
}

.dimehome .search {
    display: inline-flex;
    margin: auto;
    width: 200px;
}

.dimehome .search button {}

.redtext {
    border: 1px solid red;
    border-radius: 5px;
    color: #f00;
    margin-top: 20px;
    padding: 20px;
    text-transform: uppercase;
    font-size: 0.8em;
}

/*
Carousel Stuff
*/
#carousel-example-generic {
    margin: 20px auto;
    width: 400px;
}

#carousel-custom {
    margin: 20px auto;
    width: 400px;
}

#carousel-custom .carousel-indicators {
    margin: 10px 0 0;
    overflow: auto;
    position: static;
    text-align: left;
    white-space: nowrap;
    width: 100%;
}

#carousel-custom .carousel-indicators li {
    background-color: transparent;
    border-radius: 0;
    display: inline-block;
    height: auto;
    margin: 0 !important;
    width: auto;
}

#carousel-custom .carousel-indicators li img {
    display: block;
    opacity: 0.5;
}

#carousel-custom .carousel-indicators li.active img {
    opacity: 1;
}

#carousel-custom .carousel-indicators li:hover img {
    opacity: 0.75;
}

#carousel-custom .carousel-outer {
    position: relative;
}

.carouselextratext {
    margin: 40px;
}

.carouselextratext div {
    display: none;
}

.carouselextratext div.homefield {
    font-weight: 800;
    display: block;
}

.dropdown-submenu {
    position: relative;
}

.dropdown-submenu > .dropdown-menu {
    top: 0;
    left: 100%;
    margin-top: -6px;
    margin-left: -1px;
    border-radius: 0 6px 6px 6px;
}

.dropdown-submenu:hover > .dropdown-menu {
    display: block;
}

.dropdown-submenu > a:after {
    display: block;
    content: " ";
    float: right;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
    border-width: 5px 0 5px 5px;
    border-left-color: #ccc;
    margin-top: 5px;
    margin-right: -10px;
}

.dropdown-submenu:hover > a:after {
    border-left-color: #fff;
}

.dropdown-submenu.pull-left {
    float: none;
}

.dropdown-submenu.pull-left > .dropdown-menu {
    left: -100%;
    margin-left: 10px;
    border-radius: 6px 0 6px 6px;
}

/* mappick */

.mappick-group div {}

.mappick_fields {}

.mappick_fields label {}

.mappick {

    height: 100px;

}
/* map overlay */

.mapview .btn-group {
    margin: 10px 0;
}

.mapview .btn-group .style-select-option {
    cursor: pointer;
}

.map-cover {
    background: rgba(0, 0, 0, 1) url("../images/icons/loading.gif") no-repeat scroll center center;
    position: absolute;
    z-index: 1;
}

a.layer-select:hover {
    cursor: pointer;
}

a.layer-select.selected,
a.layer-select.selected:hover {
    background-color: #999999;
}

.map-legend {
    padding: 60px 20px;
    position: absolute;
    right: 0;
    top: 0;
    text-align: center;
    display: none;
}

.map-legend .map-legend-label {
    text-transform: uppercase;
    background-color: #f9f9f9;
    display: block;
    padding: 0 5px;
}

.map-legend .legend-image {
    margin: 5px;
}

/*  Footer */

.footer {
    color: #7f7f7f;
    height: 60px;
    background-color: #fff;
}
@media (max-width: 992px) {
    .footer {
        height: 80px;
    }

    body {
        margin-bottom: 80px;
    }
}
@media (max-width: 640px) {
    .footer {
        height: 100px;
    }

    body {
        margin-bottom: 100px;
    }
}
@media (max-width: 359px) {
    .footer {
        position: relative;
    }

    body {
        margin-bottom: 0;
    }
}

.footer .credits {
    font-weight: 100;
    text-align: center;
}

.footer .footer-right {
    text-align: right;
}

.footer .funder {
    font-size: 2em;
    color: #000;
}

.control-label.required:after {
    content: " *";
    color: red;
}
